.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.home {
  height: 100vh;
  min-height: 650px;
  background: url("../img/Bg/first-screen.jpg") no-repeat;
  background-size: cover;
  position: relative; }

.header {
  padding-top: 11px; }

.header__logo {
  float: left;
  width: 32%; }

.header__menu {
  float: right;
  width: 68%;
  color: #ffffff;
  padding-top: 20px; }

.nav {
  display: inline-block;
  vertical-align: -14px; }

.nav__list {
  width: 100%; }

.nav__item {
  float: left;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 500;
  margin-right: 15px; }
  .nav__item:after {
    content: "";
    display: inline-block;
    border-radius: 50%;
    width: 4px;
    height: 4px;
    background: #ffffff; }
  .nav__item:last-child:after {
    display: none; }

.nav__link {
  display: inline-block;
  margin-right: 15px;
  vertical-align: middle; }
  .nav__link:hover {
    text-decoration: underline; }

.nav__link, .order-link {
  color: #ffffff; }

.order-link {
  display: inline-block;
  background: #e45028;
  padding: 10px 10px;
  border-radius: 5px;
  color: #ffffff;
  vertical-align: top;
  transition: .5s; }
  .order-link:hover {
    background: #f08c33; }

.home__content {
  transform: translateY(-50%);
  position: absolute;
  left: 0;
  right: 0;
  top: 50%; }

.content-left__img {
  width: 50%;
  float: left; }

.img__burger {
  background: url("../../img/content/drops.png") no-repeat;
  background-position: 10px 65px; }

.burger-foto {
  padding-left: 40px; }

.home__content-righ {
  float: right;
  width: 50%;
  padding-top: 120px; }

.section__title {
  font-size: 72px;
  color: #f9b43b;
  font-family: gagalin; }

.section__desk {
  font-size: 65px;
  color: #ffffff;
  font-family: lifehack;
  text-transform: lowercase;
  line-height: 0.9em;
  padding-top: 17px; }

.down-arrow {
  display: block;
  position: absolute;
  left: 50%;
  bottom: 0;
  margin-bottom: 30px;
  transition: all 0.3s ease; }
  .down-arrow:hover {
    transform: scale(1.5);
    outline-width: 0; }

.best {
  background: url("../../img/Bg/we.jpg") 50% no-repeat;
  height: 100vh;
  min-height: 650px;
  background-size: cover;
  position: relative;
  text-align: center; }

.content-best {
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  left: 0;
  right: 0; }

.best__title {
  font-size: 72px;
  color: #2f3234;
  font-family: gagalin;
  margin-bottom: 80px; }

.best__item {
  display: inline-block;
  width: 33%;
  vertical-align: top; }

.best__item-icon {
  margin-bottom: 30px; }

.best__item-title {
  font-size: 24px;
  color: #2f3234;
  font-family: lifehack;
  margin-bottom: 20px; }

.burger {
  background: url("../../img/Bg/menu.jpg") center no-repeat;
  background-size: cover;
  height: 100vh;
  min-height: 650px; }

.burger__container {
  position: relative;
  transform: translateY(-50%);
  top: 50%; }

.burger-slider-wrap {
  position: relative;
  overflow: hidden;
  min-height: 490px; }

.burger-slider {
  position: absolute;
  left: 0;
  right: 0;
  white-space: nowrap;
  font-size: 0; }

.stage-outer {
  display: inline-block; }

.burger-slider__item {
  position: relative;
  padding-bottom: 85px;
  width: 940px;
  display: inline-block; }

.ingredients {
  width: 90px;
  background: #f08c33;
  border-radius: 5px;
  position: absolute;
  transition: .5s; }
  .ingredients:hover {
    background: #e45028; }
  .ingredients:hover .ingredients-dropdown {
    left: 91px; }

.ingredients__icon {
  text-align: center;
  padding-top: 14px;
  padding-bottom: 12px; }

.ingredients__icon-text {
  font-size: 14px;
  font-family: gagalin;
  font-weight: 500;
  color: #ffffff; }

.ingredients-dropdown {
  width: 270px;
  background: #ffffff;
  opacity: .9;
  position: absolute;
  border-radius: 5px;
  font-size: 14px;
  padding: 15px;
  left: 9999px;
  top: 0; }
  .ingredients-dropdown:after {
    content: "";
    width: 30px;
    height: 304px;
    position: absolute;
    left: -32px;
    top: 92px; }

.ingredients__table {
  width: 100%; }

.ingredients__table-icon {
  margin-right: 15px;
  padding: 8px 0; }

.ingredients__table-text {
  font-weight: 500;
  margin-right: 50px; }

.burger-weight {
  position: absolute;
  right: 0;
  left: 0;
  bottom: 10px; }

.burger-weight-measure {
  background: url("../../img/icons/kkal.svg") center no-repeat;
  text-align: center;
  position: relative;
  opacity: .5;
  width: 60px;
  height: 80px; }

.burger-weight-amount {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 15px;
  font-size: 20px;
  font-weight: 500;
  font-family: gagalin;
  color: #ffffff; }

.burger-weight__title {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -11px;
  font-size: 14px;
  font-weight: 500;
  font-family: gagalin;
  color: #ffffff; }

.burger-slider__left {
  width: 50%;
  float: left;
  padding-top: 85px; }

.burger-slider__img {
  text-align: center; }

.burger-slider__pic {
  width: 425px; }

.burger-slider__right {
  width: 50%;
  float: left;
  white-space: normal; }

.burger-section__title {
  font-size: 72px;
  color: #ffffff;
  font-family: gagalin;
  line-height: 1em;
  padding-bottom: 45px; }

.burger-slider__desc {
  color: #ffffff;
  font-size: 14px;
  line-height: 2em;
  font-weight: 300;
  width: 100%;
  max-width: 430px; }

.burger-slider__price {
  display: inline-block;
  font-family: gagalin;
  color: #e45028; }

.burger-slider__amount {
  font-size: 72px;
  display: inline-block; }

.burger-slider__measure {
  display: inline-block;
  font-size: 30px; }

.burger-slider__buy {
  background: #e45028;
  padding: 10px 10px;
  border-radius: 5px;
  color: #ffffff;
  font-size: 14px;
  margin-left: 24px;
  transition: all .3s ease;
  display: inline-block; }
  .burger-slider__buy:hover {
    transform: scale(1.1); }

.burger-slider__btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%); }

.slider--next {
  right: -7px;
  z-index: 10; }

.slider--prev {
  left: -6px;
  z-index: 10; }

.burger-slider__btn--next {
  transform: rotate(-90deg); }

.burger-slider__btn--prev {
  transform: rotate(90deg); }

.team {
  height: 100vh;
  min-height: 650px;
  background: url("../../img/Bg/teams.jpg") 50% no-repeat;
  background-size: cover;
  position: relative; }

.team-container {
  position: absolute;
  width: 50%;
  background: #2f3234;
  opacity: .9;
  right: 0;
  padding: 4% 2% 0% 7%;
  height: 100%; }

.team-title {
  font-size: 72px;
  color: #f9b43b;
  font-family: gagalin;
  margin-bottom: 5%; }

.team-item {
  position: relative;
  margin-bottom: 40px; }
  .team-item .team-item__content {
    display: none;
    padding-top: 14px; }

.team-item__triggle {
  font-size: 14px;
  font-family: gagalin;
  color: #ffffff;
  position: relative;
  display: block;
  cursor: pointer;
  transition: .3s; }
