.menu {
    @include section;
    background: url("../../img/Bg/burgers.jpg") center no-repeat;
    background-size: cover;
    position: relative;
}

.menu__title {
    @include title($white);
    position: absolute;
    top: 10%;
    left: 10%;
}

.menu__open {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    @extend %clearfix; 
}

.menu__item {
    float: left;
    height: 100%;
    @extend %clearfix;
        
}

.active {
    .menu__item-title {
        color: $yellow;
     }

     .menu__item-content {
        //   width: 540px;
     }
}

.menu__item-trigger {
    width: 80px;
    display: block;
    height: 100%;
    float: left;
    position: relative;
    background-repeat: no-repeat;
    background-position: 90%, center;
    background-size: cover;
     &:after {
         content: "";
         width: 100%;
         height: 100%;
         position: absolute;
         background: rgba(0,0,0,0.5);
     } 
}

.menu__item-title {
    @include title($white);
    font-size: 40px;
    transform: rotate(-90deg);
    text-align: center;
    position: absolute;
    top: 50%;
    height: 80px;
    width: 650px;
    left: 50%;
    margin-top: -40px;
    margin-left: -312px;
    // padding-top: 15px;
    z-index: 10;
    transform: 1s;
    white-space: nowrap;
        &:hover {
            color: $orange;
        }

}

.menu__item-content {
    height: 100%;
    opacity: .9;
    float: left;
    background: $black;
    width: 0;
    color: $white;
    overflow: hidden;
} 

.menu__item-text {
    width: 540px;
    padding: 20px;
}
